@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Custom styles from the user's request */

#smooth-wrapper {
    height: 100%;
    overflow: hidden;
}

#smooth-content {
    will-change: transform;
}

.animate-fadeIn {
    animation: fadeIn .5s ease forwards;
}

@keyframes fadeIn {
    0% {
        transform: scale(.98);
        opacity: 0;
    }
    to {
        transform: scale(1);
        opacity: 1;
    }
}

.text-shadow-lg {
    text-shadow: 0 1px 2px var(--tw-text-shadow-color, rgba(0,0,0,0.1)), 0 3px 2px var(--tw-text-shadow-color, rgba(0,0,0,0.1)), 0 4px 8px var(--tw-text-shadow-color, rgba(0,0,0,0.1));
}

